/* Navigation */
body.nav-opened .nav-cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 240px;
  bottom: 0;
  z-index: 200;
}
.nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  width: 240px;
  opacity: 0;
  background: #111;
  margin-bottom: 0;
  text-align: left;
  overflow-y: auto;
  -webkit-transition: -webkit-transform 0.5s ease, opacity 0.3s ease 0.7s;
  transition: transform 0.5s ease,
                        opacity 0.3s ease 0.7s;
}
body.nav-closed .nav {
  -webkit-transform: translate3D(97px, 0, 0);
  -ms-transform: translate3D(97px, 0, 0);
  transform: translate3D(97px, 0, 0);
}
body.nav-opened .nav {
  opacity: 1;
  -webkit-transition: -webkit-transform 0.3s ease, opacity 0s ease 0s;
  transition: transform 0.3s ease,
                        opacity 0s ease 0s;
  -webkit-transform: translate3D(0, 0, 0);
  -ms-transform: translate3D(0, 0, 0);
  transform: translate3D(0, 0, 0);
}
.nav-title {
  position: absolute;
  top: 45px;
  left: 30px;
  font-size: 16px;
  font-weight: 100;
  text-transform: uppercase;
  color: #fff;
}
.nav-close {
  position: absolute;
  top: 38px;
  right: 25px;
  width: 20px;
  height: 20px;
  padding: 0;
  font-size: 10px;
}
.nav-close:focus {
  outline: 0;
}
.nav-close:before,
.nav-close:after {
  content: '';
  position: absolute;
  top: 0;
  width: 20px;
  height: 1px;
  background: #969696;
  top: 15px;
  -webkit-transition: background 0.15s ease;
  transition: background 0.15s ease;
}
.nav-close:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.nav-close:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.nav-close:hover:before,
.nav-close:hover:after {
  background: #ffffff;
}
.nav ul {
  padding: 90px 9% 5%;
  list-style: none;
  counter-reset: item;
}
.nav li:before {
  display: block;
  float: right;
  padding-right: 4%;
  padding-left: 5px;
  text-align: right;
  font-size: 1.2rem;
  vertical-align: bottom;
  color: #B8B8B8;
  content: counter(item, lower-roman);
  counter-increment: item;
}
.nav li {
  margin: 0;
}
.nav li a {
  text-decoration: none;
  line-height: 1.4;
  font-size: 1.4rem;
  display: block;
  padding: 0.6rem 4%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.nav li a:after {
  display: inline-block;
  content: " .......................................................";
  color: rgba(255, 255, 255, 0.2);
  margin-left: 5px;
}
.nav .nav-current:before {
  color: #fff;
}
.nav .nav-current a:after {
  content: " ";
  border-bottom: rgba(255, 255, 255, 0.5) 1px solid;
  width: 100%;
  height: 1px;
}
.nav a:link,
.nav a:visited {
  color: #B8B8B8;
}
.nav li.nav-current a,
.nav a:hover,
.nav a:active,
.nav a:focus {
  color: #fff;
}
.navbar-toggle :nth-child(2) {
  margin-top: 13px;
  transform-origin: 33px 0;
}
.navbar-toggle :nth-child(3) {
  margin-top: 23px;
}
.navbar-toggle :nth-child(1) {
  animation: layer-one 2.33s infinite alternate;
}
.navbar-toggle :nth-child(2) {
  animation: layer-two 2.33s infinite alternate;
}
.navbar-toggle :nth-child(3) {
  animation: layer-three 2.33s infinite alternate;
}
@keyframes layer-one {
  0% {
    transform: scale(1, 1) rotateZ(0deg) translate(0, 0);
  }
  26.5% {
    transform: scale(1, 1) rotateZ(0deg) translate(0, 0);
  }
  33% {
    transform: scale(1, 1) rotateZ(25deg) translate(0, 0);
  }
  40% {
    transform: scale(1, 1) rotateZ(-60deg) translate(0, 14px);
  }
  60% {
    transform: scale(1, 1) rotateZ(-45deg) translate(0, 14px);
  }
  100% {
    transform: scale(1, 1) rotateZ(-45deg) translate(0, 14px);
  }
}
@keyframes layer-three {
  0% {
    transform: scale(1, 1) rotateZ(0deg) translate(0, 0);
  }
  26.5% {
    transform: scale(1, 1) rotateZ(0deg) translate(0, 0);
  }
  33% {
    transform: scale(1, 1) rotateZ(-25deg) translate(0, 0);
  }
  40% {
    transform: scale(1, 1) rotateZ(60deg) translate(0, -14px);
  }
  60% {
    transform: scale(1, 1) rotateZ(45deg) translate(0, -14px);
  }
  100% {
    transform: scale(1, 1) rotateZ(45deg) translate(0, -14px);
  }
}
@keyframes layer-two {
  0% {
    transform: translateX(0) scaleX(1);
  }
  26.5% {
    transform: translateX(0) scaleX(1);
  }
  33% {
    transform: translateX(-10px) scaleX(0.95);
  }
  36.5% {
    transform: translateX(28px) scaleX(1);
  }
  44% {
    transform: translateX(28px) scaleX(0);
  }
  100% {
    transform: translateX(28px) scaleX(0);
  }
}
/* Clears shit */
.clearfix:before,
.clearfix:after,
.dl-horizontal dd:before,
.dl-horizontal dd:after,
.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after,
.form-horizontal .form-group:before,
.form-horizontal .form-group:after,
.btn-toolbar:before,
.btn-toolbar:after,
.btn-group-vertical > .btn-group:before,
.btn-group-vertical > .btn-group:after,
.nav:before,
.nav:after,
.navbar:before,
.navbar:after,
.navbar-header:before,
.navbar-header:after,
.navbar-collapse:before,
.navbar-collapse:after,
.pager:before,
.pager:after,
.panel-body:before,
.panel-body:after {
  content: " ";
  display: table;
}
.clearfix:after,
.dl-horizontal dd:after,
.container:after,
.container-fluid:after,
.row:after,
.form-horizontal .form-group:after,
.btn-toolbar:after,
.btn-group-vertical > .btn-group:after,
.nav:after,
.navbar:after,
.navbar-header:after,
.navbar-collapse:after,
.pager:after,
.panel-body:after {
