.older-posts:hover,
.newer-posts:hover {
  color: #889093;
  border-color: #98a0a4;
}
.extra-pagination {
  display: none;
  border-bottom: #EBF2F6 1px solid;
}
.extra-pagination:after {
  display: block;
  content: "";
  width: 7px;
  height: 7px;
  border: #E7EEF2 1px solid;
  position: absolute;
  bottom: -5px;
  left: 50%;
  margin-left: -5px;
  background: #FFF;
  border-radius: 100%;
  box-shadow: #FFF 0 0 0 5px;
}
.extra-pagination .pagination {
  width: auto;
}
/* On page2+ make all the headers smaller */
.archive-template .main-header {
  max-height: 30vh;
}
/* On page2+ show extra pagination controls at the top of post list */
.archive-template .extra-pagination {
  display: block;
}
.site-footer {
  position: relative;
  margin: 8rem 0 0 0;
  padding: 0.5rem 15px;
  border-top: #EBF2F6 1px solid;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.75em;
  color: #BBC7CC;
}
.site-footer a {
  color: #BBC7CC;
  text-decoration: none;
  font-weight: bold;
}
.site-footer a:hover {
  color: #50585D;
}
.poweredby {
  display: block;
  width: 45%;
  float: right;
  text-align: right;
}
.copyright {
  display: block;
  width: 45%;
  float: left;
}
@media only screen and (max-width: 900px) {
  blockquote {
    margin-left: 0;
  }
  .main-header {
