    height: auto;
    min-height: 240px;
    height: 60vh;
    padding: 15% 0;
  }
  .scroll-down,
  .home-template .main-header:after {
    display: none;
  }
  .archive-template .main-header {
    min-height: 180px;
    padding: 10% 0;
  }
  .blog-logo img {
