  }
  .archive-template .main-header {
    max-height: 20vh;
    min-height: 160px;
    padding: 10% 0;
  }
  .main-nav {
    padding: 5px;
    margin-bottom: 2rem;
  }
  .blog-logo {
    padding: 5px;
  }
  .blog-logo img {
    height: 30px;
  }
  .menu-button {
    padding: 0 5px;
    border-radius: 0;
    border-color: transparent;
    color: #2e2e2e;
    background: transparent;
  }
  .menu-button:hover {
    color: #2e2e2e;
    border-color: transparent;
    background: none;
